$page-banner-inner-width: 612px;

// child variant
$page-banner-child: '.ma__page-banner--child';

.ma__page-banner {
  position: relative;
  text-align: center;


  &__background {
    background-size: cover;
    background-position: top center;
    max-width: 1600px;
    height: 400px;
    position: absolute;
      top: 0;
      left: 50%;
    transform: translateX(-50%);
    width: 100%;
    z-index: -1;

    &:after {
      @include ma-container;

      @media($bp-medium-min) {
        content: "";
        height: 10px;
        position: absolute;
          bottom: -10px;
          left: 50%;
        transform: translateX(-50%);
        width: 100%;
      }
    }
  }

  &__content {
    min-height: 400px;
    padding: 0 45px 40px;
    position: relative;
    z-index: 1;

    @media($bp-small-min) {
      margin: 0 60px;
    }

    @media($bp-medium-min) {
      margin: 0 auto;
      max-width: $page-banner-inner-width;
    }

    #{$page-banner-child} & {
      padding-top: 130px;  // linear-gradient offset
    }
  }

  &__icon {
    margin-bottom: 55px;
    text-align: center;

    #{$page-banner-child} & {
      margin-bottom: 5px;
    }

    a {
      display: inline-block;
    }
  }

  &__back-link {
    display: block;
    font-size: 0.875rem;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  &__title {
    font-size: 2.75rem;
    line-height: 1.05;
    margin-bottom: 0;
    padding-bottom: 0.67em;

      @media ($bp-medium-min){
        font-size: 3.75rem;
      }

      @media ($bp-large-min){
        font-size: 4.25rem;
      }

      @media ($bp-x-large-min){
        font-size: 4.625rem;
      }
  }

  &__divider {
    @include ma-border-decorative();
    margin: 0 auto;
    position: relative;
    width: 100px;
  }

  &__description {
    font-size: 1.375rem;
    padding-top: 35px;

    & > :last-child {
      margin-bottom: 0;
    }
  }
}

